.page-estate {
    main {
      .lead {
        padding-left: 10px;
        margin: 10px 0 10px;
      }
    }

  .search-list h3 {
    font-weight: normal;
    margin-bottom: 8px;
    margin-top: 10px;
  }

  // 物件検索ページ -----------------------------------------
  #search {
    form[name="searchForm"] {
      h3 {
        @include font-line(15);
        color: $color-primary;
        border-bottom: 2px solid $color-primary;
        text-align: left;
        padding: 10px 10px 8px;
        margin-bottom: 10px;
        img {
          margin-left: 10px;
        }
      }
    }

    .search-list {
      label {
        display: inline-block;
        margin: 0 5px 0 10px;
        &.bukken_type {
          padding-right: 54px;
        }
        input {
          vertical-align: -0.1em;
        }
      }

      input {
        &[type="checkbox"], &[type="radio"] {
          width: 16px;
          height: 16px;
          margin-right: 0.5em;
        }
        &.station {
          width: 92%;
          margin-top: 1em;
        }
      }
      select {
        &.one_columns {
          width: 100%;
        }
        &.two_columns {
          width: 45%;
        }
      }
    }

    .madori-checkbox {
      @include clearfix();
      label {
        width: 44%;
        float: left;
        display: block;
        border: thin solid #333;
        margin-bottom: 10px;
        margin-right: 5px;
        padding: 5px 10px;
      }
    }

    #money1, #money2, #tate_menseki1, #tate_menseki2, #toti_menseki1, #toti_menseki2 {
      width: 45%;
    }
    .submitBlock {
      text-align: center;
      &__button {
        width: 100%;
      }
    }
  }

  /* 検索結果一覧ページ */
  #result {
    $result: '.result';
    #{$result}__top {
      border-bottom: 2px solid $color-border;
      padding: 0 10px;
      .lead {
        @include font-line(14);
        strong {
          @include font-line(16);
          color: $color-primary;
        }
      }
    }
    .pageHF {
      margin: 0 auto 15px;
      @include font-line(10, 10);
      text-align: center;
      span {
        font-weight: 700;
        strong {
          @include font-line(20, 20);
        }
      }
    }
    .sortArea {
      height: 23.5px;
      margin-bottom: 10px;
      @include font-line(9, 9);
      display: flex;
      justify-content: flex-start;
      align-items: center;
      select {
        width: 180px;
        margin-left: 5px;
      }
    }

    /*リスト表示部分*/
    .bukkenList {
      $bukkenList: ".bukkenList";
      margin: 0;
      .bukkenItem {
        border-top: 2px solid #666;
        padding: 0;
        margin-bottom: 20px;
        &__header {
          background-color: #fff;
          margin-bottom: 10px;
          padding: 0;
          .stamps {
            height: 16px;
            margin: 10px 0;
            .type {
              background-color: transparent;
              font-weight: 700;
            }
            .new {
              background-color: $color-sub;
              border: none;
              color: #fff;
            }
            .pricedown {
              background-color: $color-main;
              border: none;
              width: 74px;
              color: #fff;
            }
          }
          .title {
            margin: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            .heading {
              margin: 0;
              line-height: 13px;
              a {
                border: none;
                padding: 0;
                font-weight: 700;
                @include font-line(14);
                line-height: 13px;
                color: $color-text01;
                text-decoration: none;
              }
            }
          }
        }
        .groupButton {
          margin: 0 0 27px;
          display: flex;
          justify-content: space-between;
          .button {
            width: 48.335%;
            i {
              padding-right: 5px;
            }
          }
        }
        &__body {
          border: none;
          padding:0;
          text-decoration: none;
          display: block;
        }
        &__thumb {
          width: 100%;
          height: 225px;
          margin: 0 auto;
          position: relative;
          img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            font-family: 'object-fit: contain;;';
          }
          .stamp {
            width: 97px;
            height: 97px;
            border-radius: 50%;
            background-color: #000;
            color: #fff;
            @include font-line(13, 13);
            @include opacity(0.6);
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            z-index: 2;
          }
        }
        &__info {
          margin-top: 7px;
          color: $color-text01;
        }
      }
    }
  }

  //------------------------========物件詳細ページ:estate/detail.html=======-----------------------
  #detail {
    $detail: ".detail";
    position: relative;
    #{$detail}__header {
      margin: 0 0 10px;
      padding: 0 0 10px;
      display: flex;
      &__left {
        .stamp {
          background-color: $color-sub;
          width: 54px;
          height: 54px;
          border-radius: 50%;
          margin-right: 10px;
          font-weight: 700;
          @include font-line(12, 12);
          color: #fff;
          display: flex;
          justify-content: center;
          align-items: center;
          letter-spacing: 0.1em;
        }
      }
      &__right {
        display: flex;
        flex-flow: column wrap;
        &__stamps {
          @include clearfix();
          margin: 0;
          padding: 5px 0 10px;
          .type {
            background-color: transparent;
          }
          .new {
            background-color: $color-sub;
            border: none;
            width: 70px;
            color: #fff;
          }
          .pricedown {
            background-color: $color-main;
            border: none;
            width: 70px;
            color: #fff;
          }
        }
        &__heading {
          margin: 0;
          padding: 0;
          font-weight: 700;
          @include font-line(14, 18);
        }
      }
    }
    .features {
      flex: auto;
      margin: 0 0 10px;
      padding: 0;
      line-height: 7.5px;
      .feature {
        background-color: #8e8e8e;
        width: 72px;
        height: 14px;
        margin-bottom: 5px;
        font-weight: 500;
        @include font-line(10, 10);
        color: #fff;
        letter-spacing: -1px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
      }
    }
    #{$detail}__description {
      margin: 0 0 20px;
      padding: 0;
      .text {
        @include font-line(12);
        margin: 0;
      }
    }
    #{$detail}__section {
      margin: 15px auto 0;
      padding: 0;
      &__heading {
        margin: 0 0 9px;
      }
      .help {
        width: 145px;
        margin: 20px 0 30px;
        i {
          padding-right: 5px;
        }
      }
      .supplement {
        @include font-line(12, 15);
      }
    }
    .slides {
      margin: 0 auto 10px;
      padding: 0;
      overflow: hidden;
      &__item {
        width: 100%;
        height: 225px;
        text-align: center;
        overflow: hidden;
        img {
          object-fit: contain;
          font-family: "object-fit: contain;";
          width: 100%;
          height: 100%;
        }
      }
      &__caption,
      &__items {
        @include font-line(8, 8);
        margin: 0 auto 10px;
        .img-kind {
          margin: 0;
        }
        .img-memo {
          margin: 0;
        }
      }
    }

    .c-box {
      @include font-line(9);
    }
    .groupButton {
      display: flex;
      justify-content: space-between;
      margin: 0;
      padding: 0;
      .button {
        width: 48.335%;
        i {
          padding-right: 5px;
        }
      }
    }
    .map-out {
      margin: 0 auto 10px;
      padding: 0;
      #map-canvas {
        width: 100%;
        height: 200px;
      }
    }
  }
}
